.modal-wrapper {
  background-color: rgba(255, 255, 255, 0.96);
  position: fixed;
  inset: 0;
  z-index: 2;
  display: none; }
  .modal-wrapper--show {
    display: block;
    transition: 0.8s; }

.modal {
  max-width: 304px;
  padding: 24px 16px 24px 16px;
  background-color: #FFFFFF;
  height: 100vh;
  position: absolute;
  right: 0;
  z-index: 3;
  box-shadow: -2px 0px 4px rgba(69, 79, 126, 0.02), 16px 0px 52px rgba(14, 24, 80, 0.2); }

.modal__button--close {
  margin-bottom: 32px; }

.modal__title {
  font-size: 24px;
  font-family: 'TT Lakes';
  font-weight: bold;
  margin-bottom: 24px; }
  .modal__title::before {
    content: "";
    border-left: 3px solid #41F6D7;
    height: 1.5em;
    border-radius: 2px;
    position: relative;
    right: 16px; }

.form__input {
  min-width: 288px;
  display: block;
  background: #FFFFFF;
  border: 1px solid #EAEAEA;
  border-radius: 8px;
  padding: 12px 16px;
  margin-bottom: 16px; }
  .form__input--size {
    height: 118px;
    resize: none; }

.form__text {
  color: #7E7E82;
  font-size: 14px;
  margin-bottom: 19px; }

.form__link {
  color: #FF3E79; }

.button--order {
  background-color: #FF3E79;
  padding: 8px 40px 8px 12px;
  border-radius: 16px;
  color: #FFFFFF;
  font-weight: 600;
  position: relative;
  font-size: 12px; }
  .button--order::after {
    content: '';
    background: url("data:image/svg+xml,%3Csvg width='7' height='10' viewBox='0 0 7 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M3.8 4.99994L0.791554 2.229C0.3641 1.83529 0.361125 1.16138 0.785088 0.763917L0.920293 0.637162C1.30321 0.278177 1.89851 0.276333 2.28364 0.632939L6.93812 4.94264C6.9715 4.97355 6.9715 5.02633 6.93812 5.05723L2.34674 9.30851C1.92606 9.69803 1.27582 9.69601 0.857554 9.30389C0.394457 8.86974 0.397706 8.13363 0.864616 7.70358L3.8 4.99994Z' fill='white'/%3E%3C/svg%3E%0A") no-repeat;
    position: absolute;
    width: 7px;
    height: 10px;
    right: 16px;
    top: 12px; }

.form__submit {
  position: absolute;
  right: 16px; }

@media (min-width: 1366px) {
  .modal {
    padding: 40px 32px 75px 32px; }
  .modal__title:before {
    right: 32px; } }
